/* 
  
   12 .
   ,     `` ``  .
*/

import { Container, Row, Col, Card, Table, Button, Form } from "react-bootstrap";
import { API_BASE_URL } from "../config/config";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";

function App({ user }) {
    const { id } = useParams(); // id  
    const [product, setProduct] = useState(null); //    

    //    state,  true   .
    const [loading, setLoading] = useState(true);

    //   
    const [quantity, setQuantity] = useState(0);

    const navigate = useNavigate();

    //  id     rendering .
    useEffect(() => {
        if (!user) {
            alert('  .');
            navigate('/member/login');
            return;
        }

        const url = `${API_BASE_URL}/product/detail/${id}`;

        axios
            .get(url, { withCredentials: true }) // ,   
            .then((response) => {
                console.log(response.data);
                setProduct(response.data);
                setLoading(false); //    .
            })
            .catch((error) => {
                console.log(error);

                if (error.response && error.response.status === 401) { // 401(UnAuthrized)
                    alert('  .');
                    navigate('/member/login'); //    

                } else {
                    alert('      .');
                    navigate(-1); //   
                }
            });
    }, [id, user, navigate]);

    //  backend      .
    if (loading === true) {
        return (
            <Container className="my-4 text-center">
                <h3>
                        .
                </h3>
            </Container>
        );
    }

    //       .
    if (!product) {
        return (
            <Container className="my-4 text-center">
                <h3>
                        .
                </h3>
            </Container>
        );
    }


    //       
    const QuantityChange = (event) => {
        // parseInt()        .
        const newValue = parseInt(event.target.value);
        setQuantity(newValue);
    };

    //   , ''  .
    const addToCart = async () => {
        if (quantity < 1) {
            alert(`  1  .`);
            return;
        }
        //alert(`${product.name} ${quantity}   `);

        try {
            const url = `${API_BASE_URL}/cart/insert`;

            // Cart   ` `, ` `, ``.
            // BackEnd  CartProductDto   .
            const parameters = {
                memberId: user.id,
                productId: product.id,
                quantity: quantity
            };

            const response = await axios.post(url, parameters, { withCredentials: true });

            alert(response.data);
            navigate('/product/list'); //    

        } catch (error) {
            console.log('  : ' + error);

            if (error.response) {
                alert('  ');
                console.log(error.response.data);
            }
        }
    }

    //  ``  .
    const buyNow = async () => {
        if (quantity < 1) {
            alert(' 1    .');
            return;
        }

        try {
            const url = `${API_BASE_URL}/order`;

            //   OrderDto, OrderItemDto   .
            // ) parameters  key  OrderDto     .
            //      1     .
            const parameters = {
                memberId: user.id,
                status: 'PENDING',
                orderItems: [{
                    productId: product.id,
                    quantity: quantity
                }]
            };

            console.log('  ');
            console.log(parameters);

            const response = await axios.post(url, parameters, { withCredentials: true });
            console.log(response.data);
            alert(`${product.name} ${quantity} .`);

            navigate('/product/list'); //   

        } catch (error) {
            console.log('  ');
            console.log(error);
        };
    };



    return (
        <Container className="my-4">
            <Card>
                <Row className="g-0">
                    {/*    */}
                    <Col md={4}>
                        <Card.Img
                            variant="top"
                            src={`${API_BASE_URL}/images/${product.image}`}
                            alt={`${product.name}`}
                            style={{ width: '100%', height: '400px' }}
                        />
                    </Col>
                    {/*        */}
                    <Col md={8}>
                        <Card.Body>
                            <Card.Title className="fd-3">
                                <h3>{product.name}</h3>
                            </Card.Title>
                            <Table striped>
                                <tbody>
                                    <tr>
                                        <td className="text-center"></td>
                                        <td>{product.price.toLocaleString()}</td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"></td>
                                        <td>{product.category}</td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"></td>
                                        <td>{product.stock.toLocaleString()}</td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"></td>
                                        <td>{product.description}</td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"></td>
                                        <td>{product.inputdate}</td>
                                    </tr>
                                </tbody>
                            </Table>

                            {/*    */}
                            {/* as={Row}    <div>  Row  . */}
                            <Form.Group as={Row} className="mb-3 align-items-center">
                                <Col xs={3} className="text-center">
                                    <strong> </strong>
                                </Col>
                                <Col xs={5}>
                                    {/*    1 , user    . */}
                                    <Form.Control
                                        type="number"
                                        min="1"
                                        disabled={!user}
                                        value={quantity}
                                        onChange={QuantityChange}
                                    />
                                </Col>
                            </Form.Group>

                            {/* ( , , ) */}
                            <div className="d-flex justify-content-center mt-3">
                                <Button variant="primary" className="me-3 px-4" href="/product/list">
                                     
                                </Button>
                                <Button variant="success" className="me-3 px-4"
                                    onClick={() => {
                                        if (!user) {
                                            alert('  .');
                                            return navigate('/member/login');
                                        } else {
                                            addToCart();
                                        }
                                    }}
                                >
                                    
                                </Button>
                                <Button variant="danger" className="me-3 px-4"
                                    onClick={() => {
                                        if (!user) {
                                            alert('  .');
                                            return navigate('/member/login');
                                        } else {
                                            buyNow();
                                        }
                                    }}
                                >
                                    
                                </Button>
                            </div>
                        </Card.Body>
                    </Col>
                </Row>
            </Card>
        </Container>
    );
}

export default App;