import axios from "axios";
import { useState } from "react";
import { Card, Container, Row, Form, Col, Button, Alert } from "react-bootstrap";
import { API_BASE_URL } from "../config/config";
import { useNavigate } from "react-router-dom";

function App() {
    //   state  
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [address, setAddress] = useState('');

    //   (Form Validation Check)  state  :       
    const [errors, setErrors] = useState({
        name: '', email: '', password: '', address: '', general: ''
    });

    const navigate = useNavigate();

    /*
        	     async/await 	        then/catch 
         	  	                  
             	                       
         	 try...catch   	  .catch()  
         	    	    Promise 
    */
    const SignupAction = async (event) => {
        event.preventDefault(); //   

        try {
            const url = `${API_BASE_URL}/member/signup`;
            const parameters = { name, email, password, address };

            // response   .
            const response = await axios.post(url, parameters, { withCredentials: true });

            if (response.status === 200) { /*  MemberController   */
                alert('  ');
                navigate('/member/login');
            }

        } catch (error) { // error :  
            if (error.response && error.response.data) {
                //      .
                setErrors(error.response.data);

            } else { //       
                console.log(errors);
                setErrors((previous) => ({ ...previous, general: '    .' }));
            }
        }
    };

    return (
        <Container className="d-flex justify-content-center align-items-center" style={{ height: '70vh' }}>
            <Row className="w-100 justify-content-center">
                <Col md={6}>
                    <Card>
                        <Card.Body>
                            <h2 className="text-center mb-4"> </h2>

                            {/*     alert   . */}
                            {/* contextual :        */}
                            {errors.general && <Alert variant="danger">{errors.general}</Alert>}

                            {/*
                                !!     boolean    .

                                isInvalid   control   .
                                 true Form.Control.Feedback      .
                            */}

                            <Form onSubmit={SignupAction}>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="  ."
                                        value={name}
                                        onChange={(event) => setName(event.target.value)}
                                        isInvalid={!!errors.name}
                                    />

                                    <Form.Control.Feedback type="invalid">
                                        {errors.name}
                                    </Form.Control.Feedback>

                                </Form.Group>

                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="  ."
                                        value={email}
                                        onChange={(event) => setEmail(event.target.value)}
                                        required
                                        isInvalid={!!errors.email}
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        {errors.email}
                                    </Form.Control.Feedback>
                                </Form.Group>

                                <Form.Group className="mb-3">
                                    <Form.Label> </Form.Label>
                                    <Form.Control
                                        type="password"
                                        placeholder="   ."
                                        value={password}
                                        onChange={(event) => setPassword(event.target.value)}
                                        required
                                        isInvalid={!!errors.password}
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        {errors.password}
                                    </Form.Control.Feedback>
                                </Form.Group>

                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="  ."
                                        value={address}
                                        onChange={(event) => setAddress(event.target.value)}
                                        required
                                        isInvalid={!!errors.address}
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        {errors.address}
                                    </Form.Control.Feedback>
                                </Form.Group>

                                <Button variant="primary" type="submit" className="w-100">
                                     
                                </Button>

                            </Form>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
}

export default App;