import { NavDropdown, Navbar, Container, Nav } from "react-bootstrap";

import { useNavigate } from "react-router-dom";

function App({ appName, user, handleLogout }) {
    const navigate = useNavigate();

    // user         .
    const renderMenu = () => {
        switch (user?.role) {
            case 'ADMIN':
                return (
                    <>
                        <Nav.Link onClick={() => navigate(`/product/insert`)}> </Nav.Link>
                        {/*       */}
                        <Nav.Link onClick={() => navigate(`/order/list`)}> </Nav.Link>
                        <Nav.Link onClick={handleLogout}> </Nav.Link>
                    </>
                );
            case 'USER':
                return (
                    <>
                        <Nav.Link onClick={() => navigate(`/cart/list`)}></Nav.Link>
                        <Nav.Link onClick={() => navigate(`/order/list`)}> </Nav.Link>
                        <Nav.Link onClick={handleLogout}> </Nav.Link>
                    </>
                );
            default:
                return (
                    <>
                        <Nav.Link onClick={() => navigate(`/member/login`)}></Nav.Link>
                        <Nav.Link onClick={() => navigate(`/member/signup`)}> </Nav.Link>
                    </>
                );
        }
    };

    return (
        <Navbar bg="dark" variant="dark" expand="lg">
            <Container>
                <Navbar.Brand href='/'>{appName}</Navbar.Brand>
                <Navbar.Brand>{user?.name}</Navbar.Brand>
                <Nav className="me-auto">
                    {/*  : Nav.Link     .  */}
                    <Nav.Link onClick={() => navigate(`/product/list`)}> </Nav.Link>

                    {renderMenu()}

                    <NavDropdown title={` `}>
                        <NavDropdown.Item onClick={() => navigate(`/fruit`)}> 1</NavDropdown.Item>
                        <NavDropdown.Item onClick={() => navigate(`/fruit/list`)}> </NavDropdown.Item>
                    </NavDropdown>
                </Nav>
            </Container>

        </Navbar >
    );
}

export default App;
