import axios from "axios";

import { useEffect, useState } from "react";
import { Button, Card, Col, Container, Form, Pagination, Row } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";

import { API_BASE_URL } from "../config/config";


/*
step 01
          .
  3   .
     .

step 02
  'ADMIN', //   
    
*/
function App({ user }) {
    //      state
    const [products, setProducts] = useState([]);

    //   state .
    const [paging, setPaging] = useState({
        totalElements: 0, //   (165)
        pageSize: 6, // 1    (6)
        totalPages: 0, //   (28)
        pageNumber: 0, //   (20)
        pageCount: 10, //    (10)
        beginPage: 0, //    
        endPage: 0, //  
        pagingStatus: '', // : "pageNumber/ totalPages "
        //  SearchDto   (field)
        searchDateType: 'all', //    
        category: '', //      
        searchMode: '', //     _ (name)   (description)
        searchKeyword: '' //    
    });

    //   " " 
    useEffect(() => {
        const url = `${API_BASE_URL}/product/list`;

        const parameters = {
            params: {
                pageNumber: paging.pageNumber,
                pageSize: paging.pageSize,
                searchDateType: paging.searchDateType,
                category: paging.category,
                searchMode: paging.searchMode,
                searchKeyword: paging.searchKeyword,
            },
            withCredentials: true
        };

        axios
            .get(url, parameters)
            .then((response) => {
                console.log('  ');
                console.log(response.data.content);
                setProducts(response.data.content || []);

                setPaging((previous) => {
                    const totalElements = response.data.totalElements;
                    const totalPages = response.data.totalPages;
                    const pageNumber = response.data.pageable.pageNumber;

                    //  pageSize      .
                    // ,      . 
                    const pageSize = response.data.pageable.pageSize;

                    //const pageCount = 10 //    

                    const beginPage = Math.floor(pageNumber / previous.pageCount) * previous.pageCount;
                    const endPage = Math.min(beginPage + previous.pageCount - 1, totalPages - 1);

                    // ) 0base +1  .
                    const pagingStatus = `${pageNumber + 1}/${totalPages} `;

                    return {
                        ...previous,
                        totalElements: totalElements,
                        totalPages: totalPages,
                        pageNumber: pageNumber,
                        pageSize: pageSize,
                        beginPage: beginPage,
                        endPage: endPage,
                        pagingStatus: pagingStatus,
                    };
                });
            })
            .catch((error) => {
                console.log(error);
            });

    }, [paging.pageNumber, paging.searchDateType, paging.category, paging.searchMode, paging.searchKeyword]);

    const navigate = useNavigate();

    //       `` ``    .
    const makeAdminButtons = (item, user, navigate) => {
        if (user?.role !== 'ADMIN') return null;

        return (
            <div className="d-flex justify-content-center">
                <Button
                    variant="warning"
                    className="mb-2"
                    size="sm"
                    onClick={(event) => {
                        event.stopPropagation();
                        navigate(`/product/update/${item.id}`);
                    }}
                >
                    
                </Button>
                &nbsp;
                <Button
                    variant="danger"
                    className="mb-2"
                    size="sm"
                    onClick={async (event) => {
                        event.stopPropagation();

                        const isDelete = window.confirm(`'${item.name}'  ?`);

                        if (isDelete === false) {
                            alert(`'${item.name}'   .`);
                            return;
                        }

                        try { //         .
                            // )    primary key     .
                            await axios.delete(`${API_BASE_URL}/product/delete/${item.id}`, {
                                withCredentials: true, //   
                            });


                            // alert (modal )   ,    .
                            alert(`'${item.name}'   .`);

                            //  id ,   state  .
                            setProducts(prev => prev.filter(p => p.id !== item.id));

                            navigate('/product/list');

                        } catch (error) {
                            console.log(error);
                            alert(`   : ${error.response?.data || error.message}`);
                        }
                    }}
                >
                    
                </Button>
            </div>
        );
    };

    return (
        <Container className="my-4">
            <h1 className="my-4">  </h1>
            <Link to={`/product/insert`}>
                {user?.role === 'ADMIN' && (
                    <Button variant="primary" className="mb-3">
                         
                    </Button>
                )}
            </Link>

            {/*    */}
            <Form className="p-3">
                <Row className="mb-3">
                    {/*    */}
                    <Col md={2}>
                        <Form.Select
                            name="searchDateType"
                            value={paging.searchDateType}
                            onChange={(e) => setPaging((previous) => ({ ...previous, searchDateType: e.target.value }))}
                        >
                            <option value='all'> </option>
                            <option value='1d'>1</option>
                            <option value='1w'>1</option>
                            <option value='1m'>1</option>
                            <option value='6m'>6</option>
                        </Form.Select>
                    </Col>

                    {/*   */}
                    <Col md={2}>
                        <Form.Select
                            name="category"
                            value={paging.category}
                            onChange={(e) =>
                                setPaging((previous) => ({
                                    ...previous,
                                    category: e.target.value,
                                    pageNumber: 0, //     0  
                                }))
                            }
                        >
                            <option value="ALL"> </option>
                            <option value="BREAD"></option>
                            <option value="BEVERAGE"></option>
                            <option value="CAKE"></option>
                        </Form.Select>
                    </Col>

                    {/*    */}
                    <Col md={2}>
                        <Form.Select
                            name="searchMode"
                            value={paging.searchMode}
                            onChange={(e) => setPaging((previous) => ({ ...previous, searchMode: e.target.value }))}
                        >
                            <option value="ALL"> </option>
                            <option value="name"></option>
                            <option value="description"> </option>
                        </Form.Select>
                    </Col>

                    {/*   */}
                    <Col md={4}>
                        <Form.Control
                            name="searchKeyword"
                            type="text"
                            placeholder="  ."
                            value={paging.searchKeyword}
                            onChange={(e) => {
                                e.preventDefault();
                                setPaging((previous) => ({ ...previous, searchKeyword: e.target.value }));
                            }}
                        />
                    </Col>

                    {/*     */}
                    <Col md={2}>
                        <Form.Control
                            as="input"
                            type="text"
                            value={paging.pagingStatus}
                            disabled
                            style={{
                                fontSize: '20px',
                                backgroundColor: '#f0f0f0',
                                textAlign: 'center', //   
                                width: '100%', //   
                                margin: '0 auto', //     
                            }}
                        />
                    </Col>
                </Row>
            </Form>


            {/*     */}
            <Row>
                {/* products  , item  1  */}
                {products.map((item) => (
                    <Col key={item.id} md={4} className="mb-4">
                        <Card className="h-100"
                            onClick={() => navigate(`/product/detail/${item.id}`)}
                            style={{ cursor: 'pointer' }}>
                            <Card.Img
                                variant="top"
                                src={`${API_BASE_URL}/images/${item.image}`}
                                alt={item.name}
                                style={{ width: '100%', height: '200px' }}
                            />
                            <Card.Body>
                                {/* borderCollapse :     ,      */}
                                <table style={{ width: '100%', borderCollapse: 'collapse', border: 'none' }}>
                                    <tbody>
                                        <tr>
                                            <td style={{ width: '70%', padding: '4px', border: 'none' }} >
                                                <Card.Title>{item.name}({item.id})</Card.Title>
                                            </td>
                                            {/*  textAlign:   , verticalAlign:     */}
                                            {/* rowSpan      colSpan  */}
                                            <td rowSpan={2} style={{ padding: '4px', border: 'none', textAlign: 'center', verticalAlign: 'middle' }} >
                                                {makeAdminButtons(item, user, navigate)}
                                            </td>
                                        </tr>
                                        <tr>
                                            <td style={{ width: '70%', padding: '4px', border: 'none' }} >
                                                <Card.Text> : {item.price.toLocaleString()} </Card.Text>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>

            {/*    */}
            <Pagination className="justify-content-center mt-4">
                {/*   */}
                <Pagination.First
                    onClick={() => {
                        console.log('First  (0  )');
                        setPaging((previous) => ({ ...previous, pageNumber: 0 }));
                    }}
                    disabled={paging.pageNumber < paging.pageCount}
                    as="button"
                >
                    
                </Pagination.First>

                <Pagination.Prev
                    onClick={() => {
                        const gotoPage = paging.beginPage - 1;
                        console.log(`Prev  (${gotoPage}  )`);
                        setPaging((previous) => ({ ...previous, pageNumber: gotoPage }));
                    }}
                    disabled={paging.pageNumber < paging.pageCount}
                    as="button"
                >
                    
                </Pagination.Prev>

                {/*     */}
                {/*     */}
                {[...Array(paging.endPage - paging.beginPage + 1)].map((_, idx) => {
                    // pageIndex   .
                    const pageIndex = paging.beginPage + idx + 1;

                    return (
                        <Pagination.Item
                            key={pageIndex}
                            active={paging.pageNumber === (pageIndex - 1)}
                            onClick={() => {
                                console.log(`${pageIndex}  `);
                                setPaging((previous) => ({ ...previous, pageNumber: (pageIndex - 1) }));
                            }}
                        >
                            {pageIndex}
                        </Pagination.Item>
                    )
                })}



                {/*   */}
                <Pagination.Next
                    onClick={() => {
                        const gotoPage = paging.endPage + 1;
                        console.log(`Next  (${gotoPage}  )`);
                        setPaging((previous) => ({ ...previous, pageNumber: gotoPage }));
                    }}
                    disabled={paging.pageNumber >= Math.floor(paging.totalPages / paging.pageCount) * paging.pageCount}
                    as="button"
                >
                    
                </Pagination.Next>

                <Pagination.Last
                    onClick={() => {
                        const gotoPage = paging.totalPages - 1;
                        console.log(`Last  (${gotoPage}  )`);
                        setPaging((previous) => ({ ...previous, pageNumber: gotoPage }));
                    }}
                    disabled={paging.pageNumber >= Math.floor(paging.totalPages / paging.pageCount) * paging.pageCount}
                    as="button"
                >
                    
                </Pagination.Last>
            </Pagination>

        </Container>
    );
}

export default App;