import axios from "axios";

import { useEffect, useState } from "react";
import { Button, Col, Container, Row, Image, Table, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

import { API_BASE_URL } from "../config/config";

function App({ user }) {
    const thStyle = { fontSize: '1.2rem' }; //   

    //   ` `  
    const [cartProducts, setCartProducts] = useState([]);

    //        
    const [orderTotalPrice, setOrderTotalPrice] = useState(0);

    const navigate = useNavigate();

    //       .
    // user?.id : Optional Chaining(      undefined  .)
    // ,          
    useEffect(() => {
        if (user && user?.id) {
            fetchCartProducts();
        }
    }, [user]);

    //     ` `  .
    const fetchCartProducts = async () => {
        try {
            const url = `${API_BASE_URL}/cart/list/${user.id}`;
            const response = await axios.get(url, { withCredentials: true });
            console.log('   ');
            console.log(response.data);

            setCartProducts(response.data || []);

        } catch (error) {
            console.log(' ');
            console.log(error);
            alert(`' '       .`);
            navigate('/product/list');
        }
    };

    // ` `   Toggle .
    const toggleAllCheckBox = (isAllCheck) => {
        // isAllCheck : ` `   boolean 
        setCartProducts((previous) => {
            //  ( )   ,  (checked) 
            // ` `    .
            const updatedProducts = previous.map((product) => ({
                ...product,
                checked: isAllCheck
            }));

            //     updatedProducts      .
            refreshOrderTotalPrice(updatedProducts);

            return updatedProducts;
        });
    };

    //    Toggle  ,     .
    const refreshOrderTotalPrice = (products) => {
        let total = 0; //   

        products.forEach((bean) => {
            if (bean.checked) { //    
                total += bean.price * bean.quantity; //   
            }
        });

        setOrderTotalPrice(total); // State 
    };

    //    .
    const toggleCheckBox = (cartProductId) => {
        console.log(`   : ${cartProductId}`);

        setCartProducts((previous) => {
            // !product.checked   toggle   .
            const updatedProducts = previous.map((product) =>
                product.cartProductId === cartProductId
                    ? { ...product, checked: !product.checked }
                    : product
            );

            refreshOrderTotalPrice(updatedProducts);
            return updatedProducts;
        });
    };

    //        .
    const changeQuantity = async (cartProductId, quantity) => {
        // NaN : Not A Number
        if (isNaN(quantity)) { //   
            //  0  ,   .
            setCartProducts((previous) => {
                previous.map((product) =>
                    product.cartProductId === cartProductId
                        ? { ...product, quantity: 0 }
                        : product
                );
            });

            alert('   1 .');
            return;
        }

        try {
            //   : 100  10 .
            // http://localhost:9000/cart/edit/100?quantity=10
            const url = `${API_BASE_URL}/cart/edit/${cartProductId}?quantity=${quantity}`;

            // patch        .
            //  WebConfig  addCorsMappings()   .
            const response = await axios.patch(url, {}, {
                withCredentials: true  //     
            });

            console.log(response.data || '');

            // cartProducts   .
            setCartProducts((previous) => {
                const updatedProducts = previous.map((product) =>
                    product.cartProductId === cartProductId
                        ? { ...product, quantity: quantity }
                        : product
                );

                refreshOrderTotalPrice(updatedProducts);
                return updatedProducts;
            });

        } catch (error) {
            console.log('    ');
            console.log(error);
        }
    };

    //   ` `      .
    const deleteCartProduct = async (cartProductId) => {
        const isConfirmed = window.confirm('    ?');

        if (isConfirmed) {
            console.log('    : ' + cartProductId);

            try {
                const url = `${API_BASE_URL}/cart/delete/${cartProductId}`;
                const response = await axios.delete(url, { withCredentials: true });

                //    ,   .
                setCartProducts((previous) => {
                    const updatedProducts = previous.filter((bean) => bean.cartProductId !== cartProductId);

                    refreshOrderTotalPrice(updatedProducts);
                    return updatedProducts;
                });

                alert(response.data);

            } catch (error) {
                console.log('    ');
                console.log(error);
            }
        } else {
            alert('` `  .');
        }
    };

    //  ``  .
    const makeOrder = async () => {
        //   on   .
        const selectedProducts = cartProducts.filter((bean) => bean.checked);
        if (selectedProducts.length === 0) {
            alert('   .');
            return;
        }

        try {
            const url = `${API_BASE_URL}/order`;

            //   OrderDto, OrderItemDto   .
            // ) parameters  key  OrderDto     .
            const parameters = {
                memberId: user.id,
                status: 'PENDING',
                orderItems: selectedProducts.map((product) => ({
                    cartProductId: product.cartProductId,
                    productId: product.productId,
                    quantity: product.quantity
                }))
            };

            console.log('  ');
            console.log(parameters);

            const response = await axios.post(url, parameters, { withCredentials: true });
            alert(response.data);

            //        .
            setCartProducts((previous) =>
                previous.filter((product) => !product.checked) //   
            );

            setOrderTotalPrice(0); //    

        } catch (error) {
            console.log('  ');
            console.log(error);
        };
    };

    return (
        <Container className="mt-4">
            <h2 className="mb-4">
                {/* xxrem   xx . */}
                <span style={{ color: 'blue', fontSize: '2rem' }}>{user?.name}</span>
                <span style={{ fontSize: '1.3rem' }}> </span>
            </h2>
            <Table striped bordered>
                <thead>
                    <tr>
                        <th style={{ ...thStyle, width: '30%' }}>
                            {/* ` `    (boolean) toggleAllCheckBox   */}
                            <Form.Check
                                type="checkbox"
                                label=" "
                                onChange={(event) => toggleAllCheckBox(event.target.checked)}
                            />
                        </th>
                        <th style={thStyle}> </th>
                        <th style={thStyle}></th>
                        <th style={thStyle}></th>
                        <th style={thStyle}></th>
                    </tr>
                </thead>
                <tbody>
                    {cartProducts.length > 0 ? (
                        cartProducts.map((product) => (
                            <tr key={product.cartProductId}>
                                <td className="text-center align-middle">
                                    {/*     toggleCheckBox  cartProductId  . */}
                                    <Form.Check
                                        type="checkbox"
                                        checked={product.checked}
                                        style={{ transform: "scale(1.4)", cursor: "pointer" }}
                                        onChange={() => toggleCheckBox(product.cartProductId)}
                                    />
                                </td>
                                <td className="text-center align-middle">
                                    <Row> {/*  4  ,  8    */}
                                        <Col xs={4}>
                                            <Image
                                                src={`${API_BASE_URL}/images/${product.image}`}
                                                thumbnail
                                                alt={product.name}
                                                width={`80`}
                                                height={`80`}
                                            />
                                        </Col>
                                        <Col xs={8} className="d-flex align-items-center">
                                            {product.name}
                                        </Col>
                                    </Row>
                                </td>
                                <td className="text-center align-middle">
                                    <Form.Control
                                        type="number"
                                        min={1}
                                        value={product.quantity}
                                        onChange={(event) =>
                                            changeQuantity(
                                                product.cartProductId,
                                                parseInt(event.target.value)
                                            )}
                                        style={{ width: '80px', margin: '0 auto' }}
                                    />
                                </td>
                                <td className="text-center align-middle">
                                    {(product.price * product.quantity).toLocaleString()} 
                                </td>
                                <td className="text-center align-middle">
                                    <Button variant="danger" size="sm"
                                        onClick={() => deleteCartProduct(product.cartProductId)}
                                    >
                                        
                                    </Button>
                                </td>
                            </tr>
                        ))
                    ) : (
                        <tr><td>  .</td></tr>
                    )}
                </tbody>
            </Table>

            {/*  (text-start),  (text-center),  (text-end) */}
            <h3 className="text-end mt-3">   : {orderTotalPrice.toLocaleString()}</h3>
            <div className="text-end">
                <Button variant="primary" size="lg" onClick={makeOrder}>
                    
                </Button>
            </div>
        </Container>
    );
}

export default App;