import axios from "axios";

import { useEffect, useState } from "react";
import { API_BASE_URL } from "../config/config";
import { Table } from "react-bootstrap";

// axios       .
function App() {
    const [fruit, setFruit] = useState({}); //    1

    useEffect(() => { // BackEnd    
        const url = `${API_BASE_URL}/fruit`; //  url 

        axios
            .get(url, {})
            .then((response) => { //    
                console.log('  ');
                console.log(response.data);

                setFruit(response.data);
            });
    }, []);

    return (
        <>
            <Table hover style={{ margin: '20px' }}>
                <tbody>
                    <tr>
                        <td></td>
                        <td>{fruit.id}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{fruit.name}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{Number(fruit.price).toLocaleString()}</td>
                    </tr>
                </tbody>
            </Table >
        </>
    );
}

export default App;
