import axios from "axios";
import { useEffect, useState } from "react";
import { Alert, Button, Card, Col, Container, Row, Spinner } from "react-bootstrap";
import { API_BASE_URL } from "../config/config";

function App({ user }) {
    // loading true     .
    const [loading, setLoading] = useState(true);

    //    
    const [error, setError] = useState('');

    //    (  :  )
    const [orders, setOrders] = useState([]);

    //  hook   user    rendering .
    useEffect(() => {
        if (!user) {
            setError(' .');
            setLoading(false);
        }

        //   OrderController getOrderList()  
        const fetchOrders = async () => {
            try {
                const url = `${API_BASE_URL}/order/list`;

                // get    , params    .
                const parameters = {
                    params: { memberId: user.id, role: user.role },
                    withCredentials: true
                };

                const response = await axios.get(url, parameters);

                //console.log(response.data);
                setOrders(response.data);

            } catch (error) {
                setError('     .');
                console.log(error);

            } finally {
                setLoading(false);
            };
        };

        fetchOrders(); //  

    }, [user]);

    const makeStatusButton = (bean) => {
        if (user?.role !== "ADMIN" && user?.role !== "USER") return null;

        // ``   `PENDING`  `COMPLETED`  .
        const changeCompleted = async (newStatus) => {
            try {
                const url = `${API_BASE_URL}/order/update/status/${bean.orderId}?status=${newStatus}`;
                await axios.put(url);

                alert(`  ${bean.orderId}   ${newStatus}  .`);

                // `COMPLETED`   ,   .
                // bean.orderId     rendering .
                setOrders((previous) =>
                    previous.filter((order) => order.orderId !== bean.orderId)
                );

            } catch (error) {
                console.log(error);
                alert(' ( ) .');
            }
        };

        // ``   ` `   .
        const orderCancel = async () => {
            try {
                const url = `${API_BASE_URL}/order/delete/${bean.orderId}`;
                await axios.delete(url, { withCredentials: true });

                alert(`  ${bean.orderId}   .`);

                // bean.orderId     rendering .
                setOrders((previous) =>
                    previous.filter((order) => order.orderId !== bean.orderId)
                );

            } catch (error) {
                console.log(error);
                alert('  .');
            }
        };

        return (
            <div className="d-flex align-items-center">
                {/*       */}
                {user?.role === 'ADMIN' && (
                    <span
                        className="me-3 px-3 py-1 border rounded fw-bold text-primary"
                        style={{
                            borderColor: '#0d6efd',   //   (Bootstrap primary)
                            backgroundColor: 'transparent', //   
                            fontSize: '0.9rem',
                        }}
                    >
                         {bean.name}
                    </span>
                )}
                {/* ``    . */}
                {user?.role === 'ADMIN' && (
                    <Button
                        variant="success"
                        size="sm"
                        className="me-2"
                        onClick={() => changeCompleted('COMPLETED')}
                    >
                        
                    </Button>
                )}

                <Button
                    variant="danger"
                    size="sm"
                    className="me-2"
                    onClick={() => orderCancel()}
                >
                    
                </Button>
            </div>
        );
    };


    if (loading) {
        return (
            <div className="d-flex justify-content-center align-items-center p-5">
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">   .</span>
                </Spinner>
            </div>
        );
    }

    if (error) {
        return (
            <Container className="my-4">
                <Alert variant="danger">{error}</Alert>
            </Container>
        );
    }

    return (
        <Container className="my-4">
            <h1 className="my-4"> </h1>
            {orders.length === 0 ? (
                <Alert variant="secondary">  .</Alert>
            ) : (
                <Row>
                    {orders.map((bean) => (
                        <Col key={bean.orderId} md={6} className="mb-4">
                            <Card
                                className="h-100 shadow-sm"
                                style={{
                                    border: '2px solid #495057'
                                }}
                            >
                                <Card.Body>
                                    <div className="d-flex justify-content-between">
                                        <Card.Title>  : {bean.orderId}</Card.Title>
                                        <small className="text-muted">{bean.orderDate}</small>
                                    </div>

                                    <Card.Text>
                                         : <strong>{bean.status}</strong>
                                    </Card.Text>

                                    <ul style={{ paddingLeft: "20px" }}>
                                        {bean.orderItems.map((item, index) => (
                                            <li key={index}>
                                                {item.productName}({item.quantity})
                                            </li>
                                        ))}
                                    </ul>

                                    {/*      */}
                                    {makeStatusButton(bean)}

                                </Card.Body>

                            </Card>
                        </Col>
                    ))}
                </Row>
            )}
        </Container>
    );
}

export default App;