import './App.css';

//    import
import MenuItems from './ui/MenuItems';
import AppRouters from './routes/AppRouters';

import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

function App() {
   const appName = "IT Academy Coffee Shop";

   // user :       state
   //      localStorage .
   const [user, setUser] = useState(null);

   // JSON.parse() JSON        .
   useEffect(() => {
      const loginUser = localStorage.getItem('user');
      setUser(JSON.parse(loginUser));
   }, []); // 2   empty array 1 rendering.

   const handleLoginSuccess = (userData) => {
      // userData : LoginPage.js   member .
      setUser(userData);
      localStorage.setItem('user', JSON.stringify(userData));
      console.log(' ');
   }

   const navigate = useNavigate();

   //   ' '  .
   const handleLogout = (event) => {
      event.preventDefault();

      setUser(null);
      localStorage.removeItem('user');
      console.log('  ');
      navigate(`/member/login`);
   };

   return (
      <>
         <MenuItems appName={appName} user={user} handleLogout={handleLogout} />

         {/*    */}
         <AppRouters user={user} handleLoginSuccess={handleLoginSuccess} />

         <footer className="bg-dark text-light text-center py-3 mt-5">
            <p>&copy; 2025 {appName}. All rights reserved.</p>
         </footer>
      </>
   );
}

export default App;
