import axios from 'axios';
import { useEffect, useState } from 'react';
import { Alert, Button, Container, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { API_BASE_URL } from "../config/config";

/*
      " "  .

step 01 : 
  .

ControlChange 
       change   .
    (input type) : , , ,  
    (combo type) : 

FileSelect     
          .
    FileReader API    Base64     .

SubmitAction 
       BackEnd . 

   
      post .
    input  type="file"  .

 
      "product_"     .    
      1   .
      1 1   .
*/

function App({ user }) {
    const navigate = useNavigate();

    const comment = ' ';

    useEffect(() => {
        if (!user || user.role !== 'ADMIN') {
            alert(`${comment} ()   .`);
            navigate('/');
        }
    }, [user, navigate]);

    const initial_value = {
        name: '', price: '', category: '', stock: '', image: '', description: ''
    }; //   

    // product        
    const [product, setProduct] = useState(initial_value);

    //    (  )
    const [errors, setErrors] = useState({
        name: '',
        price: '',
        category: '',
        stock: '',
        image: '',
        description: '',
        general: ''
    });


    //      .
    const ControlChange = (event) => {
        // event  change    .
        const { name, value } = event.target;
        //console.log(`   : ${name},  : ${value}`);

        //        .
        setProduct({ ...product, [name]: value });
    }

    const FileSelect = (event) => {
        //         .
        const { name, files } = event.target;
        const file = files[0]; // type="file"  1 

        // FileReader     ,    .
        //         .
        const reader = new FileReader();

        // readAsDataURL()  file   (Base64 )   .
        reader.readAsDataURL(file);

        // onloadend :        
        reader.onloadend = () => {
            const result = reader.result;
            //console.log(result);

            //   Base64    state .
            //   : data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUA...
            setProduct({ ...product, [name]: result });
        };
    };

    const SubmitAction = async (event) => {
        event.preventDefault();

        if (product.category === "-") {
            alert('    .');
            return; //  
        }

        try {
            const url = `${API_BASE_URL}/product/insert`;
            //   : 2   .
            const parameters = product;

            //   :    .
            // const parameters = {...product};

            //   : JSON.parse() JSON.stringify()   

            // Content-Type(Mime Type) :       
            //  : 'text/html', 'image/jpeg', 'application/json' 
            //   json  .            
            const config = {
                headers: { 'Content-Type': 'application/json' },
                withCredentials: true
            };

            const response = await axios.post(url, parameters, config);

            console.log(`  : [${response.data}]`);
            alert('   .');

            //        .
            setProduct(initial_value);

            setErrors({}); //  

            //         .
            navigate('/product/list');

        } catch (error) {
            if (error.response && error.response.data) {
                //     
                setErrors((prev) => ({
                    ...prev,
                    ...error.response.data.errors,
                    general: error.response.data.message || '    .'
                }));
            } else {
                setErrors((prev) => ({
                    ...prev,
                    general: '    .'
                }));
            }
        };
    };

    return (
        <Container style={{ marginTop: '30px' }}>
            <h1>{comment}</h1>

            {/*    */}
            {errors.general && <Alert variant="danger">{errors.general}</Alert>}

            <Form onSubmit={SubmitAction}>
                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="()  ."
                        name="name"
                        value={product.name}
                        onChange={ControlChange}
                        isInvalid={!!errors.name}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.name}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="()  ."
                        name="price"
                        value={product.price}
                        onChange={ControlChange}
                        isInvalid={!!errors.price}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.price}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Select
                        name="category"
                        value={product.category}
                        onChange={ControlChange}
                        isInvalid={!!errors.category}
                    >

                        {/* )  Enum       .  */}
                        <option value="-">--   .</option>
                        <option value="BREAD"></option>
                        <option value="BEVERAGE"></option>
                        <option value="CAKE"></option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                        {errors.category}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="()  ."
                        name="stock"
                        value={product.stock}
                        onChange={ControlChange}
                        isInvalid={!!errors.stock}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.stock}
                    </Form.Control.Feedback>
                </Form.Group>

                {/*  type="file" ,       . */}
                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="file"
                        name="image"
                        onChange={FileSelect}
                        isInvalid={!!errors.image}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.image}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label> </Form.Label>
                    <Form.Control
                        type="text"
                        placeholder=" ()  ."
                        name="description"
                        value={product.description}
                        onChange={ControlChange}
                        isInvalid={!!errors.description}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.description}
                    </Form.Control.Feedback>
                </Form.Group>

                <Button variant='primary' type='submit' size='lg'>
                    {comment}
                </Button>

            </Form>
        </Container >
    );
}

export default App;