import axios from 'axios';
import { useEffect, useState } from 'react';
import { Alert, Button, Container, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { API_BASE_URL } from "../config/config";
// useParams  url       .
import { useParams } from "react-router-dom";

/*
  .

   
       id   .
    id          .(useEffect  )

step 01 : 
 ` `   .

   
    get       .

 
       ''  ,      .

  ' ' .
    ControlChange    
    FileSelect   

ControlChange 
       change   .
    (input type) : , , ,  
    (combo type) : 

FileSelect     
          .
    FileReader API    Base64     .

SubmitAction 
    ''   '' .
    'insert'  'update/${id}' .
    'axios.post'  'axios.put' .
       BackEnd . 

   
      post .
    input  type="file"  .

 
      "product_"      .    
           .
          .
         .   
*/

function App({ user }) {
    const { id } = useParams();
    console.log(`   : ${id}`);

    const comment = ' ';

    const initial_value = {
        name: '', price: '', category: '', stock: '', image: '', description: ''
    }; //   

    // product        
    const [product, setProduct] = useState(initial_value);

    //    (  )
    const [errors, setErrors] = useState({
        name: '',
        price: '',
        category: '',
        stock: '',
        image: '',
        description: '',
        general: ''
    });


    const navigate = useNavigate();

    // id      
    useEffect(() => {
        if (!user || user.role !== 'ADMIN') {
            alert(`${comment} ()   .`);
            navigate('/');
        }

        const url = `${API_BASE_URL}/product/update/${id}`;

        axios
            .get(url, { withCredentials: true })
            .then((response) => {
                setProduct(response.data);
            })
            .catch((error) => {
                console.log(` ${id}   : ${error}`);
                alert('     .');
            });

    }, [id, user, navigate]); // id      re-rendering  .


    //      .
    const ControlChange = (event) => {
        // event  change    .
        const { name, value } = event.target;
        //console.log(`   : ${name},  : ${value}`);

        //        .
        setProduct({ ...product, [name]: value });
    }

    const FileSelect = (event) => {
        //         .
        const { name, files } = event.target;
        const file = files[0]; // type="file"  1 

        // FileReader     ,    .
        //         .
        const reader = new FileReader();

        // readAsDataURL()  file   (Base64 )   .
        reader.readAsDataURL(file);

        // onloadend :        
        reader.onloadend = () => {
            const result = reader.result;
            //console.log(result);

            //   Base64    state .
            //   : data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUA...
            setProduct({ ...product, [name]: result });
        };
    };


    const SubmitAction = async (event) => {
        event.preventDefault();

        if (product.category === "-") {
            alert('    .');
            return; //  
        }

        try {
            // )    ${id}  .
            const url = `${API_BASE_URL}/product/update/${id}`;

            //   : 2   .
            const parameters = product;

            //   :    .
            // const parameters = {...product};

            //   : JSON.parse() JSON.stringify()   

            // Content-Type(Mime Type) :       
            //  : 'text/html', 'image/jpeg', 'application/json' 
            //   json  .            
            const config = {
                headers: { 'Content-Type': 'application/json' },
                withCredentials: true
            };
            // put()   ""    .
            const response = await axios.put(url, parameters, config);

            console.log(`  : [${response.data}]`);
            alert('   .');

            //        .
            setProduct(initial_value);

            setErrors({}); //  

            //         .
            navigate('/product/list');

        } catch (error) {
            if (error.response && error.response.data) {
                //     
                setErrors((prev) => ({
                    ...prev,
                    ...error.response.data.errors,
                    general: error.response.data.message || '    .'
                }));
            } else {
                setErrors((prev) => ({
                    ...prev,
                    general: '    .'
                }));
            }
        };
    };

    return (
        <Container style={{ marginTop: '30px' }}>
            <h1>{comment}</h1>

            {/*    */}
            {errors.general && <Alert variant="danger">{errors.general}</Alert>}

            <Form onSubmit={SubmitAction}>
                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="()  ."
                        name="name"
                        value={product.name || ''}
                        onChange={ControlChange}
                        isInvalid={!!errors.name}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.name}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="()  ."
                        name="price"
                        value={product.price || ''}
                        onChange={ControlChange}
                        isInvalid={!!errors.price}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.price}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Select
                        name="category"
                        value={product.category || ''}
                        onChange={ControlChange}
                        isInvalid={!!errors.category}                    >

                        {/* )  Enum       .  */}
                        <option value="-">--   .</option>
                        <option value="BREAD"></option>
                        <option value="BEVERAGE"></option>
                        <option value="CAKE"></option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                        {errors.category}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="()  ."
                        name="stock"
                        value={product.stock || ''}
                        onChange={ControlChange}
                        isInvalid={!!errors.stock}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.stock}
                    </Form.Control.Feedback>
                </Form.Group>

                {/*  type="file" ,       . */}
                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control
                        type="file"
                        name="image"
                        onChange={FileSelect}
                        isInvalid={!!errors.image}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.image}
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label> </Form.Label>
                    <Form.Control
                        type="text"
                        placeholder=" ()  ."
                        name="description"
                        value={product.description || ''}
                        onChange={ControlChange}
                        isInvalid={!!errors.description}
                    />
                    <Form.Control.Feedback type="invalid">
                        {errors.description}
                    </Form.Control.Feedback>
                </Form.Group>

                <Button variant='primary' type='submit' size='lg'>
                    {comment}
                </Button>

            </Form>
        </Container >
    );
}

export default App;