import axios from "axios";
import { useState } from "react";
import { Container, Row, Col, Card, Alert, Form, Button } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { API_BASE_URL } from "../config/config";

function App({ setUser }) {
    // setUser :     setter 

    //   state 
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    //    state 
    const [errors, setErrors] = useState('');

    const navigate = useNavigate();

    const LoginAction = async (event) => { //     
        event.preventDefault();
        console.log("  ...");

        try {
            const url = `${API_BASE_URL}/member/login`;

            // axios   json  ,    .
            //  form-urlencoded   .
            // URLSearchParams   QueryString    .
            const parameters = new URLSearchParams();
            parameters.append('email', email);
            parameters.append('password', password);

            console.log(email + '/' + password);

            //      Map<String, Object> .
            const response = await axios.post(url, parameters, {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                withCredentials: true //    
            });

            // message '  '  , member       .
            const { message, member } = response.data;


            if (message === 'success') { //  .put("message", "success")   .
                console.log('   ');
                console.log(member);

                //       .  
                setUser(member);

                navigate(`/`); //      

            } else { //  
                setErrors(message);
            }

        } catch (error) {
            if (error.response) {
                setErrors(error.response.data.message || ' ');
                alert(error.response.data.error);  //     

            } else {
                setErrors('Server Error');
            }
        }
    }

    return (
        <Container className="d-flex justify-content-center align-items-center" style={{ height: '70vh' }}>
            <Row className="w-100 justify-content-center">
                <Col md={6}>
                    <Card>
                        <Card.Body>
                            <h2 className="text-center mb-4"></h2>

                            {errors && <Alert variant="danger">{errors}</Alert>}

                            <Form onSubmit={LoginAction}>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="  ."
                                        value={email}
                                        onChange={(event) => setEmail(event.target.value)}
                                        required
                                    />
                                </Form.Group>

                                <Form.Group className="mb-3">
                                    <Form.Label> </Form.Label>
                                    <Form.Control
                                        type="password"
                                        placeholder="   ."
                                        value={password}
                                        onChange={(event) => setPassword(event.target.value)}
                                        required
                                    />
                                </Form.Group>

                                <Row className="g-2">
                                    <Col xs={8}>
                                        <Button variant="primary" type="submit" className="w-100">
                                            
                                        </Button>
                                    </Col>
                                    <Col xs={4}>
                                        <Link to={`/member/signup`} className="btn btn-outline-secondary w-100">
                                             
                                        </Link>
                                    </Col>
                                </Row>
                            </Form>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
}

export default App;